body {
  font-family: 'Open-Sans', sans-serif;
  color: var(--main-color);
  margin: 0;
  padding: 0;


}

.body {
  --main--color: #FF5D55;


}

.body.active {
  --main--color: #63ff55;
  --main--color: #3AB0FF;
}


* {
