  }
}

.container {
  margin: 0px auto;
  position: relative;
  padding: 0px 20px;
}

/* Start Home */

#changecolor {
  position: absolute;
  width: 50px;
  height: 20px;
  left: 10%;
  top: 85%;
  cursor: pointer;
  background-color: var(--main--color);
  z-index: 1000;
}

.totop {
  position: absolute;
}

.totop {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  left: 90%;
  top: 85%;
  position: fixed;
  cursor: pointer;
  background-color: var(--main--color);
  z-index: 1000;
  padding: 15px;
}

.totop::after {
  content: "";
  width: 100%;
  height: 100%;
  border-width: 10px;
  border-color: var(--main--color) var(--grayColor);
  border-style: solid;
  border-radius: 50%;
  left: -16.4%;
  top: -17%;
  transform: translate(0%, 0%);
  position: absolute;
  animation: rotate1 2s linear infinite;
  cursor: pointer;
  z-index: 1000;
}

@keyframes rotate1 {
  100% {
    transform: rotate(5.5turn);
  }
}




@media (max-width: 992px) {


  .totop {
    width: 65px;
    height: 65px;
    border-radius: 50%;
    left: 85%;
    top: 85%;
    position: fixed;
    cursor: pointer;
    background-color: var(--main--color);
    z-index: 1000;
    padding: 15px;
    /* position: relative; */
  }

