}

.home {

  position: relative;
  background-color: var(--blackColor);
  padding: 40px 0px;
  border-bottom: 5px solid var(--main--color);
}

.home::before {
  content: "";
  position: fixed;
  width: 300%;
  height: 100%;
  background-color: var(--main--color);
  left: 50%;
  top: 50%;
  transform: translate(-50%, 45%) skew(-80deg);
  border-radius: 50%;
  z-index: 10;
  animation: waving 5s ease-in-out infinite alternate;
}

@keyframes waving {

  to {
    transform: translate(-50%, 45%) skew(80deg);

  }
}

.home {
  height: 100vh;
  width: 100%;
}



.home .container {
  height: 100vh;
}

.home .container .nav {
  padding-top: 50px;
}

.home .container .nav .bars {
  background-color: #333;
  width: max-content;
  padding: 15px 20px;
  height: 50px;
  height: 300px;
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
  position: absolute;
  left: 0px;
  top: 30px;
}

.home .container .nav .bars i {
  font-size: 1.5em;
}

.home .container .nav ul {
  position: absolute;
  padding: 15px 20px;
  left: 0px;
  top: 70px;
}

.home .container .nav ul li {
  padding-bottom: 20px;
  font-size: 1.5em;
}



.home .container .nav ul li i {
  width: 55px;
}

.home .container .nav li {
  list-style: none;
  cursor: pointer;

}

.home .container .nav li a {
  text-decoration: none;
  color: white;

}

.home .container .nav li a {
  text-decoration: none;
  color: var(--whiteColor);
  transition: color 0.5s;
  font-size: 1rem;
  Font-weight: 700;
  line-height: 22px;
}

.home .container .nav li a:hover {
  color: var(--main--color);
}

.home .container .nav li a span {
  width: 160px;
  padding: 7px;
  transition: 0.5s;
  cursor: pointer;
}

.home .container .nav li a span:hover {
  border-left: var(--main--color) 15px solid;
}

.home .container .homeContent {
  width: 70%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  row-gap: 100px;
  column-gap: 50px;
}

@media (max-width: 768px) {
  .home .container .homeContent {
    width: 70%;
