    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    row-gap: 100px;
    column-gap: 50px;
  }
}

.personal-photo {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  position: relative;
}

.personal-photo::after {
  content: "";
  position: absolute;
  border-style: solid;
  border-width: 15px;
  border-color: var(--main--color);
  border-radius: 50%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  animation: move 1.5s infinite;
  z-index: -1;
}

@keyframes move {
  0% {
    width: 220px;
    height: 220px;
  }

  100% {
    width: 250px;
    height: 250px;
    opacity: 0;
  }
}

