  grid-gap: 40px;
  margin-top: 80px;
}




.Contact .container .my-Contact {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 40px;
}



.Contact .container .my-Contact div {
  position: relative;
  height: 115px;
  width: 115px;
  background-color: var(--grayColor);
  border-radius: 50%;
  border: solid 1px var(--main--color);
  box-shadow: 0px 0px 5px var(--main--color);
  cursor: pointer;
  transition-duration: 0.5s;

}




.Contact .container .my-Contact div:hover {
  transform: translate(0, -10px);
  box-shadow: 0px 2px 25px var(--main--color);
  background-color: var(--blackColor);


}


.Contact .container .my-Contact div h1 {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.5em;
  font-weight: 400;
  line-height: 20px;

}

.Contact .container .my-Contact div a i {
  font-size: 2.5em;
  color: var(--main--color);

}


.copyright {

  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: var(--whiteColor);
  margin-top: 200px;
  text-align: center;
}

.copyright span {
  color: var(--main--color);
}

@media (max-width: 576px) {

  .Contact .container .info-Contact {

    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
}

.ContactIcon {
  color: var(--main--color);
  font-size: 35px;
}

/* End Contact */